body {
  margin: 0;
  padding: 0;
  font-family: Tahoma, Geneva, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  /* background-color: #E8E8E8; */
  background-color: #D1E3F9;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  /* background-color: #E8E8E8 */
  background-color: #D1E3F9;
  margin-top: 4px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  font-family: Tahoma, Geneva, sans-serif;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.login-link {
  color: #515151;
  position: absolute;
  left: -25px;
  top: 5px;
  z-index: 9;
  width: 200px;
}

.login-link:hover {
  color: #353535;
  cursor: pointer;
  text-decoration: underline;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.wrapper-left{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 70%;
  max-width: 860px;
  margin-right: 60px;
  margin-left: 50px;
}

.title-block{
  /* background-color:#468B5C; */
  /* background-color:#609EEA; */
  background-color: #3a69a3;
  width: 100%;
  height: 70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 25px;
  margin-bottom: 15px;
  border-radius: 10px;
  font-family: Tahoma, Geneva, sans-serif;}

.title-text{
  color: white;
  font-weight: bold;
  font-size: 35px;
}

.wrapper-right{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 30%;
  margin-right: 50px;
}

