.branding-wrapper{
  display:-webkit-flex;
  display:flex;
}

.logo{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 25px;
  margin-left: 5px;
}

.logo-img{
  width: 70px;
  height: 70px;
}

.branding-block{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  margin-top: 25px;
  line-height: 200%;
  font-family: Tahoma, Geneva, sans-serif;
  text-transform: capitalize;
  font-weight: bold;
  margin-left: 25px;
}

.school{
  font-weight: bold;
  font-size: 18px;
}
.lab{
  font-weight: bold;
  font-size: 18px;
}

.axis path,
.axis line {
    fill: none;
    stroke: #000;
    shape-rendering: crispEdges;
}

.x.axis path {
    display: none;
}

.line {
    fill: none;
    stroke: steelblue;
    stroke-width: 1.5px;
}
#svg{
    border-style: solid;
    border-radius: 2px;
    background-color: white;
}

.loading-icon {
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
}


.hashtags {
    background-color: #282c34;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    border-radius: 20px;
    margin-top: 10px;
    margin-bottom: 20px
}
.Hashtag {
    color: white;
    font-weight: bold;
    margin: 10px;
    padding: 10px;
    border-radius: 20px;
    border: 2px solid white;

}

.Hashtag:hover {
    cursor: pointer;
    border: 2px solid lightgray;
    color: lightgray;
}

.selected {
    border: 2px solid lightgray;
    color: lightgray;
    opacity: 80%;
}
.graph {
    width: 100%;
    border: 4px solid lightgrey;
    background-size: 400px;
    margin-bottom: 15px;
    border-radius: 20px;
}

