.tweet-feed {
    width: 100%;
    margin-top: 20px;
    height: 528px;
    border: 4px solid #47A1EC;
    border-radius: 10px;
    margin-bottom: 20px;
    overflow: auto;
}

.header {
	height: 50px;
	line-height: 50px;
	margin-bottom: 1px;
    font-weight: bold;
    background-color: #47A1EC;
    color: white;
    font-size: 20px;
}
.tweet {
    position: relative;
    min-height: 70px;
    background-color: #F8F8F8;
    margin-bottom: 3px;
    text-align: left;
    font-size: 14px;
    padding-bottom: 12px;
    font-family: arial;
}

.tweet-image {
    width: 48px;
    height: 48px;
    position: absolute;
    top: 12px;
    left: 15px;
    margin: auto;
}

.tweet-author {
    padding-top: 12px;
    margin-left: 80px;
}

.tweet-text {
    margin-left: 80px;
    margin-top: 5px;
    margin-right: 15px;
}

.header{
    font-family: Tahoma, Geneva, sans-serif;}
.Login {
    position: absolute;
    width: 200px;
    height: 225px;
    top: 0;
    left: 200px;
    margin-left: -160px;
    z-index: 8;
    background-color: white;
    border-radius: 5px;
    border: 4px solid #515151;

}

.Login h1 {
    color: #515151;
    font-size: 15pt;
}

.login-form {
    margin-top: 30px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
}


.login-input {
    width: 150px;
    margin: 10px auto;
    border-radius: 10px;
    outline: none;
    padding: 3px;
    height: 20px;
    border: none;
    background: #ededed;
}

.login-form input[type="submit"] {
    width: 75px;
    border: none;
    margin: auto;
    font-weight: bold;
    outline: none;
    color: white;
    border-radius: 15px;
    background-color: #515151;
    height: 25px;
}

.login-form input[type="submit"]:hover {
    cursor: pointer;
    background-color: darkgray;
}


.close-button {
    color: #515151;
    position: absolute;
    left: 10px;
    top: 10px;
    outline: none;
    border: none;
}

.close-button:hover {
    color: #353535;
    cursor: pointer;
}

.Console {
    width: 500px;
    height: 500px;
    margin: 100px auto;
    color: #515151;

}


.current {
    margin: 50px;
    background: white;
    padding: 10px;
    border-radius: 30px;
}

.back-button:hover {
    text-decoration: underline;
    cursor: pointer;
}
.admin-button {
    border: none;
    outline: none;

    background-color: #515151;
    color: white;
    padding: 5px 10px;
    font-weight: bold;
    margin: 10px;
    border-radius: 15px;
}

.admin-button:hover {
    background-color: #353535;
    cursor: pointer;
}

.submit-form input[type="text"] {
    width: 150px;
    margin: 10px auto;
    border-radius: 10px;
    outline: none;
    padding: 3px;
    height: 20px;
    border: none;
    background: #ededed;
}
.submit-form input[type="submit"]{
    background-color: #515151;
    color: white;
    outline: none;
    border: none;
    border-radius: 10px;
    margin: 5px;
    font-weight: bold;
    padding: 5px;

}


.submit-form input[type="submit"]:hover {
    background-color: #353535;
    cursor: pointer;
}

.remove-button {
    background: none;
    color: red;
    font-weight: bold;
    outline: none;
    border: none;
}

.remove-button:hover {
    cursor: pointer;
}





